@import '../../global.scss';
.contact{
    background: rgba(54, 183, 147, 0.1);
    display: flex;
    @include mobile{
        flex-direction: column;
       justify-content: center;
      }

    .left{
        flex: 1;
        overflow: hidden;
        
        img{
            height: 100%;
            @include mobile{
                height: auto;
              }
        }
    }
    .right{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;

        h2{
            font-size: 30px;
        }
        form{
            width: 70%;
            height: 70%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            gap: 20px;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;
                padding: 10px;
                outline: none;
                border-radius: 10px;
                border: 0;
                @include mobile {
                    width: 100%;
                    
                  }

            }
            textarea{
                width: 300px;
                font-size: 14px;
                resize: none;
                padding: 10px;
                outline: none;
                border: 0;
                border-radius: 10px;
                @include mobile {
                    width: 100%;
                    
                  }
               
                
            }
            button{
                width: 100px;
                height: 35px;
                color: white;
                background: crimson;
                border: 0;
                border-radius: 10px;
                cursor: pointer;
                font-size: 15px;
                font-weight: 500;
            }
        }
    }
}