@import '../../global.scss';
.testmonial{
    background: rgba(191, 35, 225, 0.37);
    display: flex;
    flex-direction: column;
    align-items: center;
    
    h1{
        margin-top: 20px;
        font-size: 40px;
        color: white;
        @include mobile{
            margin-bottom: 10px;
          }
    }

    .container{
        width: 100%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile{
            flex-direction: column;
            height: 100%;
            
            
          }

        .card{
            width: 300px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0 0 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            transition: transform 1s ease;
            background: rgb(239, 234, 234);
            cursor: pointer;
            @include mobile{
               margin-bottom: 15px;
               height: 220px;
               width: 90%;
               &:nth-child(2){
                margin-bottom: 0;
               }
               
              }

            &.featured{
                width: 350px;height: 75%;
                margin: 0 30px;
                @include mobile{
                    height: 260px;
                    margin-bottom: 15px;
                    width: 90%;
                  }
            }

            &:hover{
                transform: scale(1.1);
            }

            .top{
                
                display: flex;
                align-items: center;
                justify-content: start;
                img{
                    &.left,&.right{
                      height: 25px;
                    }
                    &.user{
                        height: 60px;
                        width: 60px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 20px;
                    }

                }

            }
            .center{
                width: 90%;
                padding:0 0px;
                margin: 0 auto;
                text-align: justify;
                
            }
            .bottom{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                h4{
                    color: gray;
                    margin-top: 5px;
                }
            }
        }
    }
}