@import "../../global.scss";
.works {
  background: rgb(218, 47, 81);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;
    @include mobile {
      display: none;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);
    }
    &.right {
      right: 100px;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;
    @include mobile {
      flex-direction: column;
      height: 100vh;
      justify-content: center;
      overflow: hidden;
    }

    .container {
      width: 100vw;
      align-items: center;
      display: flex;
      justify-content: center;
      overflow: hidden;

      .item {
        background-color: white;
        width: 700px;
        height: 100%;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile {
          width: 90%;
          height: 240px;
          margin: 10px 0;
        }

        .left {
          flex: 4;
          height: 80%;

          display: flex;
          align-items: center;
          justify-content: center;
          @include mobile {
            display: none;
            
          }

          .leftContainer {
            width: 90%;
            height: 90%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: coral;
              display: flex;
              align-items: center;
              justify-content: center;

              img {
                width: 25px;
              }
            }
            h2 {
              font-size: 20px;

            }
            p {
              font-size: 13px;
              overflow:hidden; 
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;

          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          @include mobile {
           
          }

          img {
            width: 400px;
            transform: rotate(-10deg);
            object-fit: cover;
            height: 130%;
            @include mobile {
              width: 300px;
              object-fit: cover;
            }
          }
        }
      }
    }
  }
}
